import { base_url } from "@/utils/const";
import Step from "./step";
import { useI18n } from "@/hooks";

type LangType = {
  step1: string,
  step2: string,
  step3: string
}
const langs: { [key: string]: LangType } = {
  default: {
    step1: "",
    step2: "",
    step3: ""
  },
  zh: {
    step1: "启用扩展",
    step2: "授权",
    step3: "开始使用"
  },
  en: {
    step1: "Enable extension",
    step2: "Authorization",
    step3: "Get Started"
  }
}

export default function IOSStep({ step }: { step: number }) {
  const i18n = useI18n<LangType>(langs);
  const stepConfigs = [
    { step: 1, name: i18n["step1"], href: base_url + "/ios/step-1/?auto=0" },
    { step: 2, name: i18n["step2"], href: base_url + "/ios/step-2/?auto=0" },
    { step: 3, name: i18n["step3"], href: base_url + "/ios/step-3/?auto=0" },
  ];
  return <Step step={step} configs={stepConfigs} />;
}
